.contacts-footer {
    display: flex;
    justify-content: space-between;

    &__link {
        font-size: 16px;
        line-height: 1.25;
        cursor: pointer;
        color: #2E2520;
        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
        background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    &__link:hover,
           :focus {
           color: $colorWhite;
    }
           &__phone-icon {
               width: 13px;
               height: 12px;
               margin-right: 4px;
               fill: currentColor;
           }
    
           &__location-icon {
               width: 11px;
               height: 13px;
               margin-right: 4px;
               fill: currentColor;
           }
}

.footer-socials {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;


    &__item:not(:last-child) {
    margin-right: 8px;
     }

    &__link {
    display: block;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: none;
    cursor: pointer;
    color: #2E2520;
    background-color: transparent;
    padding: 4px;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        background-color: $colorBlack;
        color: $colorWhite;
    }
    }


    &__icon {
    width: 16px;
    height: 16px;
    fill: currentColor;
    }
}