.registration {
    padding-bottom: 32px;
}

.form {
    width: 280px;
    height: 466px;
    text-align: center;

    padding: 33px 8px 31px 8px;
    margin: 0 auto;

    box-shadow: -4px -4px 4px rgba(171, 129, 108, 0.15), 4px 4px 4px rgba(171, 129, 108, 0.15);
    border-radius: 150px 150px 0px 0px;
    border: 1px;

    background-color: $bgColorPrimary;

    @media screen and (min-width: 768px) {
        display: flex;
        align-items: center;
        width: 704px;
        height: 260px;

        padding: 40px 61px 40px 61px;
        border-radius: 150px;
    }

    @media screen and (min-width: 1200px) {
        width: 822px;
        height: 260;
        padding: 35px 107px 35px 105px;
    }
}

.form__caption {
    font-size: 24px;
    font-weight: 400;
    line-height: 2.71;
    font-family: $fontFamilyTitle;

    color: $fontColorPrimary;

    @media screen and (max-width: 767px) {
        display: none;
    }

    @media screen and (min-width: 768px) {
        text-align: center;
    }
}

.form__title {
    font-size: 24px;
    font-weight: 400;
    line-height: 1.26;
    font-family: $fontFamilyTitle;

    color: $fontColorPrimary;

    @media screen and (min-width: 768px) {
        display: none;
    }
}

.form__info {
    border-bottom: 1px solid rgba(171, 129, 108, 0.5);
    padding: 20px 0 20px 0;

    @media screen and (min-width: 768px) {
        border-bottom: 0;
        border-right: 1px solid rgba(171, 129, 108, 0.5);

    }

    @media screen and (min-width: 1200px) {
        padding: 0;
    }
}

.form__location {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.22;

    padding-bottom: 20px;
    color: $fontColorPrimary;

    @media screen and (min-width: 768px) {
        text-align: end;
        padding: 0 32px 0 0;
    }

    @media screen and (min-width: 1200px) {
        font-size: 20px;
        padding: 20px 32px 32px 0;
    }
}

.form__price {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.22;

    @media screen and (min-width: 768px) {
        // text-align: end;
        justify-content: end;
    }
}

.form__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.21;

    margin: 8px 0 20px 0;
    color: $fontColorPrimary;

    @media screen and (min-width: 768px) {
        margin: 0 0 16px 24px;
        text-align: start;
    }

    @media screen and (min-width: 1200px) {
        font-size: 18px;
        margin: 0 0 13px 32px;
    }
}

.form__form {
    margin-bottom: 32px;

    @media screen and (min-width: 768px) {
        margin-bottom: 0;
        margin: 0 54px 0 24px;
    }

    @media screen and (min-width: 1200px) {
        margin: 0 23px 0 32px;
    }
}

.form__figuration {
    max-width: 265px;
    height: 22px;

    &:not(:last-child) {
        margin-bottom: 20px;
    }

    @media screen and (min-width: 768px) {
        width: 237px;

        &:not(:last-child) {
            margin-bottom: 16px;
        }
    }

}

.form__input {
    width: 100%;

    border: none;
    border-bottom: 1px solid $fontColorSecondary;

    font-size: 16px;
    font-weight: 400;
    line-height: 1.21;

    color: rgba(171, 129, 108, 0.5);
    background-color: $bgColorPrimary;
}

.form__button {
    font-family: $fontFamilyTitle;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.26;

    padding: 8px 47px 9px 46px;
    border-radius: 25px;
    border: none;

    color: $colorWhite;
    background-color: $fontColorSecondary;
}