@import '~node_modules/modern-normalize/modern-normalize.css';

body {

    font-family: 'Montserrat';
    font-style: normal;
    color: #2E2520;
  background-color: $bgColorPrimary;

}

.container {

    margin: 0 auto;
    padding: 0 15px;
    width: 100%;

    @media (min-width: 320px) {
        max-width: 320px;
    }

    @media (min-width: 480px) {
        max-width: 480px;
    }

    @media (min-width: 768px) {
        max-width: 768px;
    }

    @media (min-width: 1280px) {
        max-width: 1280px;
    }
}
